.sidebar {
  flex: 1;
  height: calc(100vh - 50px);
  background-color: rgb(251, 251, 255);
  position: sticky;
  top: 50px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}

.sidebarMenu {
  margin-bottom: 10px;
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem:hover {
  background-color: rgb(240, 240, 255);
}

.sidebarIcon {
  margin-right: 5px;
  font-size: 20px !important;
}

.link.active>.sidebarListItem {
  background-color: rgb(240, 240, 255);
}

@media (max-width: 768px) {

  .sidebar{
    width: 50px;
  }

  .sidebarTitle{
    display: none; 
  }
  .sidebarListItem span{
    display: none;
  }

  .sidebarListItem {
    padding: 5px;
    justify-content: center;
  }

  .sidebarWrapper {
    padding: 7px;
  }
  
}